article {
  background: #fff;
  box-shadow: 0 0 10px 5px #efefef;
  width: 320px;
  margin: 0 auto;
  margin-bottom: 40px;
}
.title {
  margin: 40px 0;
}
.title h2 {
  font-size: 36px;
  text-align: center;
  border-bottom: 3px solid #673ab7;
  width: max-content;
  margin: 0 auto;
  padding-bottom: 5px;
}
.read-more {
  color: #673ab7;
  cursor: pointer;
}
.loading {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  font-size: 40px;
  color: #673ab7;
}
img {
  width: 100%;
  height: 260px;
  object-fit: cover;
}
p {
  color: #000000ad;
  line-height: 1.7;
}
.no-tours-left button {
  width: fit-content;
  background: #673ab7;
  color: #fff;
}
.no-tours-left {
  text-align: center;
}
button {
  border: 2px solid #673ab7;
  padding: 15px 20px;
  width: 100%;
  background: white;
  text-transform: uppercase;
  font-weight: 700;
  color: #673ab7;
  cursor: pointer;
  transition: 0.4s;
  margin-top: 15px;
}
button:hover {
  background-color: #673ab7;
  color: #fff;
}
.tour-info h4 {
  margin-top: 0;
  margin-right: 15px;
}
.tour-price {
  color: #009688;
  background: #00968821;
  padding: 10px;
  width: max-content;
  font-size: 14px;
  white-space: nowrap;
  margin-top: 0;
}
.tour-info {
  display: flex;
  justify-content: space-between;
  align-items: flex-start;
}
footer {
  padding: 15px;
}
